@import "../App.css";
@import "../App.scss";

@mixin enlarge($sc: 1.1, $isFont: false) {
  @if $isFont {
    font-size: $sc + rem;
  } @else {
    transform: scale($sc);
  }
}

.round {
  border-radius: 50%;
}

.none {
  display: none;
}

.navigatePagesSection {
  width: 100%;
  display: flex;
  justify-content: center;
}

.navigatePagesBox {
  background-color: var(--secondary-color);
  width: 300px;
  padding: 0.5rem;
  margin: 0.5rem 0;
  border-radius: 10px;

  .pageNumber {
    color: white;
    @include enlarge(1.3, true);
  }

  .navigatePrev,
  .navigateNext {
    background-color: var(--primary-color);
    color: white;
    border: none;
    padding: 1rem;
    cursor: pointer;
    margin: 0 1.3rem;
    @include enlarge(1.1);
    &:hover {
      background-color: lighten($primary-color, 3%);
    }
  }
}

.none {
  display: none;
}
