@import "../App.css";

.mainMovieSection {
  width: 100%;
  display: flex;
  justify-content: center;
}

.Similar {
  .highlightHeading {
    color: darken(white, 20%);
    margin-left: 1rem;
  }

  .movieHeadingSection {
    margin: 3rem 1rem;
    display: flex;
    width: 100%;
    justify-content: center;
  }

  .movieHeading {
    text-align: center;
    color: white;
    letter-spacing: 1px;
    font-size: 1.6rem;
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--secondary-color);
  }

  .spinnerSection {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .homebtn {
    border: none;
    background-color: transparent;
    text-decoration: underline;
    color: white;
    margin-left: 1rem;
    margin-top: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.4s ease-in-out;
    z-index: 1;

    &:hover {
      color: darken(white, 15%);
    }
  }
}

.topLeft {
  position: fixed;
  top: 0;
  left: 0;
}
